module.exports = "\r\n /********************* Shopping Demo-2 **********************/\r\n\r\n.demo{padding:45px 0}\r\n\r\n.product-grid2{font-family:'Open Sans',sans-serif;position:relative}\r\n\r\n.product-grid2 .product-image2{overflow:hidden;position:relative}\r\n\r\n.product-grid2 .product-image2 a{display:block}\r\n\r\n.product-grid2 .product-image2 img{width:100%;height:auto}\r\n\r\n.product-image2 .pic-1{opacity:1;transition:all .5s}\r\n\r\n.product-grid2:hover .product-image2 .pic-1{opacity:0}\r\n\r\n.product-image2 .pic-2{width:100%;height:100%;opacity:0;position:absolute;top:0;left:0;transition:all .5s}\r\n\r\n.product-grid2:hover .product-image2 .pic-1{opacity:1}\r\n\r\n.product-grid2 .social{padding:0;margin:0;position:absolute;bottom:12px;right:25px;z-index:1}\r\n\r\n.product-grid2 .social li{margin:0 0 10px;display:block;-webkit-transform:translateX(100px);transform:translateX(100px);transition:all .5s}\r\n\r\n.product-grid2:hover .social li{-webkit-transform:translateX(0);transform:translateX(0)}\r\n\r\n.product-grid2:hover .social li:nth-child(2){transition-delay:.15s}\r\n\r\n.product-grid2:hover .social li:nth-child(3){transition-delay:.25s}\r\n\r\n.product-grid2 .social li a{color:#505050;background-color:#fff;font-size:17px;line-height:45px;text-align:center;height:35px;width:35px;border-radius:50%;display:block;transition:all .3s ease 0s}\r\n\r\n.product-grid2 .social li a:hover{color:#fff;background-color:#3498db;box-shadow:0 0 10px rgba(0,0,0,.5)}\r\n\r\n.product-grid2 .social li a:after,.product-grid2 .social li a:before{content:attr(data-tip);color:#fff;background-color:#000;font-size:12px;line-height:22px;border-radius:3px;padding:0 5px;white-space:nowrap;opacity:0;-webkit-transform:translateX(-50%);transform:translateX(-50%);position:absolute;left:50%;top:-30px}\r\n\r\n.product-grid2 .social li a:after{content:'';height:15px;width:15px;border-radius:0;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg);